}
@media (min-width: 767.98px) {
  h1,
  .h1 {
    font-size: 3rem;
  }
}
@media (min-width: 991.98px) {
  h1,
  .h1 {
    font-size: 4.5rem;
  }
}

h2,
.h2 {
  font-size: 2rem;
}
@media (min-width: 991.98px) {
  h2,
  .h2 {
    font-size: 3rem;
  }
}

h3,
.h3 {
  font-size: 1.75rem;
}
@media (min-width: 991.98px) {
