}

.text-lg {
  font-size: 1.125rem;
}

.text-base {
  font-size: var(--fs-base);
}

.text-md {
  font-size: 0.875rem;
}

.text-sm {
  font-size: 0.75rem;
}

.font-regular {
  font-weight: normal;
}

.font-medium {
  font-weight: medium;
}

.font-semibold {
  font-weight: 600;
}

.font-bold {
  font-weight: bold;
}

.font-style-normal {
  font-style: normal;
}

.font-style-italic {
  font-style: italic;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.section {
  margin-bottom: 6rem;
}
.section:last-child {
  margin-bottom: 7.5rem;
}
.section-supheader {
  display: flex;
  align-items: center;
  margin-bottom: 0.75rem;
}
@media (min-width: 991.98px) {
  .section-supheader {
    margin-bottom: 1rem;
  }
}
.section-header {
  margin-bottom: 1.25rem;
}
@media (min-width: 991.98px) {
  .section-header {
    margin-bottom: 1.5rem;
  }
