.slider {
  width: 100%;
  -webkit-appearance: none;
  height: 6px;
  background: #f0f2f5;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  position: relative;
  z-index: 2;
}

/*  */
.slider::-webkit-slider-runnable-track {
  width: 100%;
  height: 6px;
  background: #f0f2f5;
  border-radius: 5px;
  position: relative;
}

.slider::-moz-range-track {
  width: 100%;
  height: 6px;
  background: #f0f2f5;
  border-radius: 5px;
}

/*   */
.slider::-webkit-slider-runnable-track {
  background: linear-gradient(to right, #7a42f4 var(--progress, 0%), #f0f2f5 var(--progress, 0%));
}

.slider::-moz-range-progress {
  background: #7a42f4;
  height: 6px;
}

/*   */
.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 18px;
  height: 18px;
  background: #7a42f4;
  border-radius: 50%;
  cursor: pointer;
  margin-top: -6px; /*    */
  position: relative;
  z-index: 3;
}

.slider::-moz-range-thumb {
  width: 18px;
  height: 18px;
  background: #7a42f4;
  border-radius: 50%;
  cursor: pointer;
}

/* ===============================
=            Choices            =
=============================== */
.choices {
  position: relative;
  overflow: hidden;
  margin-bottom: 0px;
  font-size: inherit;
}
.choices:focus {
  outline: none;
}
.choices:last-child {
